@import url("./utils/reset.css");
@import url("./utils/utils.css");

:root {
    --header-color: rgb(0, 0, 0, 0.9);
    --app-background: rgb(24, 23, 32);
}

.container {
    width: min(100% - 30px, 1080px);
    margin-inline: auto;
}

body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

/*------*/
/*header*/
/*------*/
header {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    background-color: var(--header-color);
    color: #cb326d;
    z-index: 9999;
}

header h1 {
    font-size: 1.9rem;
    font-family: "Oxygen", sans-serif;
    font-weight: 700;
    font-style: normal;
    text-transform: uppercase;
    line-height: 60px;
    letter-spacing: 0.2px;
}

nav ul {
    display: flex;
    gap: 15px;
    padding-right: 15px;
    margin-right: 15px;
    margin-top: 0.2px;
    border-right: 0.1px solid #ddd;
}

nav ul li {
    font-size: 1.7rem;
    font-family: 600;
    line-height: 30px;
    cursor: pointer;
    color: #fff;
    transition: border-bottom ease-in-out 200ms;
    border-bottom: 0.2px solid transparent;
}

nav ul li:hover {
    border-bottom: 0.2px solid #cb326d;
}

.button {
    display: block;
    width: 80px;
    height: 34px;
    font-size: 1.7rem;
    font-weight: 600;
    border-radius: 50px;
    background-color: #cb326d;
    cursor: pointer;
    padding-bottom: 1px;
}

.button:hover {
    background-color: rgba(203, 50, 109, 0.85);
    transition: background-color 0.25s ease-in-out;
}


/*------*/
/*banner*/
/*------*/
.banner {
    position: relative;
    background: url(../media/banner.jpg) center center / cover no-repeat;
    height: 580px;
    z-index: 0000;
}

.banner article {
    position: absolute;
    top: 53%;
    left: 37%;
    margin-inline: auto;
    transform: translate(-50%, -50%);
    transition: all ease-in-out 0.25s;
    width: 55ch;
}

.big-text {
    text-align: left;
    font-size: clamp(50px, 4.45vw, 63px);
    font-weight: 550;
    font-family: "Raleway", sans-serif;
    color: #fff;
    letter-spacing: -1px;
    line-height: 0.95;
}

.bottom-text {
    text-align: left;
    font-size: clamp(15px, 1.30vw, 25px);
    font-weight: 350;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif ;
    color: #f4f4f4;
    letter-spacing: 1px;
}

article p span {
    color: gold;
}


/*------*/
/*banner buttons*/
/*------*/
.primary, .secondary {
    margin-top: 30px;
    width: 135px;
    height: 46px;
    font-size: 1.8rem;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    padding-bottom: 1px;
    display: inline-block;
    margin-right: 20px;
}

.primary {
    background-color: #FC3856;
    color: #000;
}

.primary:hover {
    background-color: rgba(252, 56, 85, 0.85);
    transition: background-color ease-in-out 150ms;
}

.secondary {
    background-color: transparent;
    color: #FC3856;
    border: 2px solid #FC3856;
}

.secondary:hover {
    background-color: rgba(0, 0, 0, 0.15);
    transition: background-color ease-in-out 150ms;
}

/*------*/
/*banner buttons*/
/*------*/
.gallery {
    text-align: center;
    min-height: 0;
}

.gallery h1 {
    display: block;
    color: #cb326d;
    border-top: 0.5px double #00DBB6;
    margin-top: 50px;
    padding-top: 30px;
    margin-bottom: -70px;
    font-family: "Oxygen", sans-serif;
    font-weight: 400;
    font-size: 30px;
    text-transform: uppercase;
    letter-spacing: -0.05rem;
}

.flex figure {
    margin: 100px 0;
    border-radius: 10px;
    padding: 3.5px;
    cursor: pointer;
}

.flex figure img {
    height: 300px;
    width: 220px;
    border-radius: 9px;
    box-shadow: 0px 0px 12px 1px rgb(255, 255, 255, 0.1);
}

.flex figure p {
    color: #f2eff0;
    font-size: 17.5px;
    margin-top: 5px;
    padding-bottom: 1px;
    text-align: center;
    transform: translate(0, -40px);
}

.flex figure:hover {
    img {
        box-shadow: 0px 0px 10px 0px #00DBB6;
        transition: box-shadow ease-in-out 150ms;
    }
    
    p {
        color: rgba(0, 219, 183);
        transition: color ease-in-out 150ms;
    }
}

.wrap {
    flex-direction: row;
    flex-wrap: wrap;
    transition: flex 0.25s ease-in-out;
    justify-content: space-evenly;
    gap: 1rem;
}


/*------*/
/*footer*/
/*------*/
footer {
    background-color: rgb(24, 10, 32);
    width: 100%;
    margin-top: auto;
}

footer section {
    display: flex;
    gap: 2rem;
    margin: 0.2px;
}

footer section p {
    cursor: pointer;
    border-bottom: 0.2px solid transparent;
    transition: border-bottom ease-in-out 200ms;
}

footer section p:hover {
    border-bottom: 0.2px solid #00DBB6;
}

footer section p, footer p {
    color: #fff;
    font-size: 17px;
    font-weight: 400;
    font-family: sans-serif;
    letter-spacing: 1px;
    line-height: 50px;
}


/*------*/
/*media queries*/
/*------*/
@media (min-width: 1440px) {
    .container {
        width: min(100% - 30px, 1080px);
        margin-inline: auto;
        transition: width ease-in-out 0.25s;
    }

    .banner article {
        left: 37%;
        transition: left ease-in-out 0.25s;
    }

    .flex figure img {
        height: 300px;
        width: 220px;
        transition: width ease-in-out 0.25s;
    }
}

@media (min-width: 1600px) {
    .container {
        width: min(100% - 30px, 1600px);
        margin-inline: auto;
        transition: width ease-in-out 0.25s;
    }

    .banner article {
        left: 37%;
        transition: left ease-in-out 0.25s;
    }

    .flex figure {
        height:auto;
        width: auto;
    }

    .flex figure img {
        height: 400px;
        width: 300px;
        transition: width ease-in-out 0.25s;
    }

    .gallery h1 {
        margin-bottom: auto;
    }
}

@media (max-width: 967px) {
    .flex figure img {
        height: 320px;
        width: 240px;
        transition: width ease-in-out 0.25s;
    }

    .gallery {
        margin-inline: 70px;
        margin-bottom: 50px;
    }

    .flex figure {
        height: 320px;
        width: auto;
        margin: 30px auto;
    }

    .banner article {
        position: absolute;
        top: 60%;
        left: 50%;
        margin-inline: auto;
        transform: translate(-50%, -50%);
        width: 60ch;
        transition: all ease-in-out 0.25s;
    }

    .gallery h1 {
        margin-bottom: auto;
    }

    .primary, .secondary {
        width: 116px;
        height: 40px;
        transition: all ease-in-out 0.25s;
    }
}

@media (max-width: 670px) {
    nav ul {
        display: none;
    }

    .banner article {
        width: 40ch;
    }
}

